<template>
    <div class="diversion">
        <!-- <van-list
            ref="list"
            v-model="loading"
            :finished="finished"
            finished-text=""
            @load="getNews">
            <card
                v-for="(card,i) in cardList"
                :key="i"
                :news-list="card.documentList"
                :title="card.title"
            />
        </van-list>
        <div
            class="more-news"
            @click="goApp">查看更多热门资讯</div> -->
    </div>
</template>

<script>
import { goBrowerDownload } from '@/utils/share';
import { getHotNewsByTime } from '@/api/hot';
import config from '@/config';
import Card from './card';


export default {
    name: 'DiversionNews',
    components: {
        Card,
    },
    data() {
        return {
            config,
            loading: false,
            finished: false,
            startTime: null,
            cardList: [],
            schema: 'bikan://goto/newsTab',
        };
    },
    methods: {
        getNews() {
            getHotNewsByTime({ startTime: this.startTime - 86400000 }).then((ret) => {
                this.loading = false;
                this.cardList.push(...ret.newsList.filter(v => v.documentList.length));
                if (ret.newsList.length > 0) {
                    this.finished = !ret.hasMore;
                    this.startTime = this.cardList[this.cardList.length - 1].millisAtStartOfDay;
                } else {
                    this.finished = true;
                }
            }).catch(() => {
                this.loading = false;
                this.finished = true;
            });
        },
        goApp(src) {
            if (src === 'top') {
                this.$o2o({
                    e_c: '资讯分享页',
                    e_a: '点击',
                    e_n: 'back中间页顶部banner',
                });
            }
            // goApp(this.schema);
            goBrowerDownload({
                page: 'read',
                source: 'news',
                channel: 'xiaomi_tuwen.apk',
                schema: this.schema,
            });
        },
    },
    mounted() {
        this.$root.hidePlaceholder();
    },
};
</script>

