<template>
    <div
        class="card customize-expose"
        :data-expose="getTrackData()"
    >
        <div class="hot-title">{{ title }}</div>
        <div
            class="news Hairline Hairline_bottom"
            v-for="news in newsList"

            :key="news.docId"
            @click="deeplinkApp(news)">
            <img :src="news.images[0]">
            <div class="info">
                <p class="title">{{ news.title }}</p>
                <div class="footer">
                    {{ news.authorName }}
                </div>
            </div>
            <div class="open-in-app">打开想看阅读</div>
        </div>
    </div>
</template>

<script>
import { goBrowerDownload, goAppStore } from '@/utils/share';
import { getDetailDeeplink } from '@/utils/jsApiV2/deeplink';

export default {
    props: {
        newsList: {
            type: Array,
            default: () => [],
        },
        title: {
            type: String,
            required: true,
        },
    },
    mounted() {
        this.$expose();
    },
    methods: {
        getTrackData() {
            return JSON.stringify({
                e_c: '资讯分享页',
                e_a: '曝光',
                e_n: 'back页今日热点',
                e_x: {
                    type: '图文',
                },
            });
        },
        deeplinkApp(news) {
            this.$o2o({
                e_c: '资讯分享页',
                e_a: '点击',
                e_n: 'back页今日热点',
                e_x: {
                    type: '图文',
                },
            });

            if (/source=liulanqi/.test(location.href)) {
                goAppStore(getDetailDeeplink(news.type, news.docId), 'CK1430771235862');
            } else {
                goBrowerDownload({
                    page: 'read',
                    source: 'news',
                    title: news.title,
                    channel: 'xiaomi_tuwen.apk',
                    schema: getDetailDeeplink(news.type, news.docId),
                });
            }
            // goApp(getDetailDeeplink(news.type, news.docId));
        },
    },
};
</script>

